@import '../../components/sso/newvault/styles/variables';
@import '../../components/sso/newvault/styles/mixins';
@import '../../components/sso/newvault/styles/utility';
@import '../../components/sso/newvault/styles/grid';
@import '../../components/sso/newvault/styles/typography';
@import '../../components/sso/newvault/styles/buttons';
@import '../../components/sso/newvault/styles/tile';

/**
  Overrides
 */
#inContextTutorialWelcomeDialog,
#inContextTutorialCompleteDialog {

  top: 10px;
  right: 10px;
  left: auto;
  bottom: auto;
  background-color: $gray-tint-4;

  span {
    font-size: $font-size-xs;
  }

  .txt-semibold {
     font-size: 14px;
     color: $charcoal
   }

  .tile-container {
    background-color: $white;
    border-top: 1px solid $gray-tint-45;
    border-bottom: 1px solid $gray-tint-45;

    .tile {
      @include shadow(0 0 0 $white); // disable shadow

      margin-top: 0;

      .row {
        position: absolute;
        left: 0;
        right: 0;

        &:hover {
          background-color: $gray-tint-4;
        }
      }
    }

    .tile-sm {
      height: 48px;
    }
  }

  .btn-base {
    height: 36px;
    width: auto;
    text-transform: uppercase;
    font-weight: $weight-bold;
    &.btn-primary {
      font-weight: $weight-semibold;
    }
    &.btn-small {
      font-size: $font-size-xxxs;
    }
    &.btn-link {
      color: $gray-shade-4;
    }
    &:not(.btn-link){
      border-radius: 100px;
      padding: 0 24px 0 24px;
      height: 36px;
    }
  }

  .dialogHeader {
    display: none;
  }
}
#inContextTutorialWelcomeDialog {
  width: 300px;
  height: 397px;
}
#inContextTutorialCompleteDialog {
  width: 320px;
  height: 300px;
}
/**
  End Overrides
 */

.dialogContent {
  padding: 0;
}
.dialog {
  margin-top: 1px;
}
.tile .row .text {
  width: auto;
}
.bg-white {
  background-color: $white;
}
.btn-close {
  margin-top: 15px;
  cursor: pointer;
}
.phone-icon {
  width: 90px;
}
